import React from "react";
import { useTypewriter, Cursor } from "react-simple-typewriter";

const TyperWriter = () => {
  const words = [
    "Software Developer",
    "GATE CSE AIR 61",
    "IIT Kharagpur",
    "Educator",
    "Machine Learning Developer",
  ];
  const { text } = useTypewriter({
    words,
    loop: 0,
  });
  return (
    <h5 className="text_light">
      {text}
      <Cursor cursorStyle="_" />
    </h5>
  );
};

export default TyperWriter;
