import React from "react";
import { BsLinkedin } from "react-icons/bs";
import { BsGithub } from "react-icons/bs";
import { BsFacebook } from "react-icons/bs";
import { BsInstagram } from "react-icons/bs";

const HeaderSocial = () => {
  return (
    <div className="header_socials">
      <a
        href="https://www.linkedin.com/in/biplab-roy-4b63b316a/"
        target="_black"
      >
        <BsLinkedin></BsLinkedin>
      </a>
      <a href="https://github.com/Biplab-Roy" target="_black">
        <BsGithub></BsGithub>
      </a>
      <a href="https://www.facebook.com/roybiplabofficial" target="_black">
        <BsFacebook></BsFacebook>
      </a>
      <a href="https://www.instagram.com/roybiplabofficial/" target="_black">
        <BsInstagram></BsInstagram>
      </a>
    </div>
  );
};

export default HeaderSocial;
