import React, { useRef } from "react";
import "./Contact.css";
import emailjs from "@emailjs/browser";
import { MdOutlineEmail } from "react-icons/md";
import { RiMessengerLine } from "react-icons/ri";
import { ImWhatsapp } from "react-icons/im";

const Contact = () => {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();
    emailjs.sendForm(
      "service_27k98fq",
      "template_0uw8cp7",
      form.current,
      "hklhziHTLwsX8pzWU"
    );
    e.target.reset();
  };
  return (
    <section id="contact">
      <h5>Get In Touch</h5>
      <h2>Contact Me</h2>
      <div className="container contact_container">
        <div className="contact_options">
          <article className="contact_option">
            <MdOutlineEmail className="content_option-icon"></MdOutlineEmail>
            <h4>Email</h4>
            <h5>roybiplabofficial@gmail.com</h5>
            <a href="mailto:roybiplabofficial@gmail.com" target="_blank">
              Send a message
            </a>
          </article>
          <article className="contact_option">
            <RiMessengerLine className="content_option-icon"></RiMessengerLine>
            <h4>Messenger</h4>
            <h5>Biplab Roy</h5>
            <a href="https://m.me/roybiplabofficial" target="_blank">
              Send a message
            </a>
          </article>
          <article className="contact_option">
            <ImWhatsapp className="content_option-icon"></ImWhatsapp>
            <h4>Whatsapp</h4>
            <h5>+91 8240950538</h5>
            <a
              href="https://api.whatsapp.com/send?phone=+918240950538"
              target="_blank"
            >
              Send a message
            </a>
          </article>
        </div>
        <form ref={form} onSubmit={sendEmail}>
          <input
            type="text"
            name="name"
            placeholder="Your Full Name"
            required
          />
          <input type="email" name="email" placeholder="Your Email" required />
          <textarea
            name="message"
            rows="7"
            placeholder="Your Message"
            required
          ></textarea>
          <button type="submit" className="btn btn-primary">
            Send Message
          </button>
        </form>
      </div>
    </section>
  );
};

export default Contact;
