import React from "react";
import "./About.css";
import me from "../../assets/image.jpg";
import { FaAward } from "react-icons/fa";
const About = () => {
  return (
    <section id="about">
      <h5>Get To Know</h5>
      <h2>About Me</h2>
      <div className="container about_container">
        <div className="about_me">
          <img src={me} alt="me" className="about_me-image" height={500} />
        </div>
        <div className="about_content">
          <div className="about_cards">
            <article className="about_card">
              <FaAward className="about_icon"></FaAward>
              <h5>School</h5>
              <small>Narikel Danga High School</small>
            </article>
            <article className="about_card">
              <FaAward className="about_icon"></FaAward>
              <h5>Graduation</h5>
              <small>Guru Nanak Institute of Technology</small>
            </article>
            <article className="about_card">
              <FaAward className="about_icon"></FaAward>
              <h5>Masters</h5>
              <small>IIT Kharagpur</small>
            </article>
          </div>
          <p>
            I am Biplab Roy, from Kolkata West Bengal. Currently, I live in the
            technology world. I love to explore things, travel, and cricket. I
            am passionate about Technology. Currently, I am doing my Masters of
            Computer Science at Indian Institute of Technology Kharagpur
            (IITKGP). I am currently focusing on learning, and upskilling
            myself. I regularly attend meetups to build a strong network. I like
            to treat people with respect. I enjoy helping people and I love to
            learn about new technologies.
          </p>
          <a href="#contact" className="btn btn-primary">
            Let's Talk
          </a>
        </div>
      </div>
    </section>
  );
};

export default About;
