import React from "react";
import "./Portfolio.css";
import sortingVisualizer from "../../assets/sortingVisualization.jpeg";
import spamDetectionModel from "../../assets/spamDetectionModel.jpeg";
import handwrittenDigitClassifier from "../../assets/handwrittenDigitClassifier.jpeg";
import durgaDarshan from "../../assets/durgaDarshan.jpg";

const data = [
  {
    id: 1,
    image: sortingVisualizer,
    title: "Sorting Visualization",
    github: "https://github.com/Biplab-Roy/Easy-Sorting",
    demo: "https://biplab-roy.github.io/Easy-Sorting/",
  },
  {
    id: 2,
    image: spamDetectionModel,
    title: "Spam Detection Model",
    github: "https://github.com/Biplab-Roy/SpamDetectorModel",
    demo: "https://spamdetectormodel.herokuapp.com/",
  },
  {
    id: 3,
    image: handwrittenDigitClassifier,
    title: "Handwritten Digit Classifier",
    github: "https://github.com/Biplab-Roy/HandwrittenDigitClassifier",
    demo: "https://handwrittendigitrecognizer.herokuapp.com/",
  },
  {
    id: 4,
    image: durgaDarshan,
    title: "An application for Durga Puja routes in WB",
    github: "https://github.com/Biplab-Roy/Durga_Darshan",
    demo: "https://github.com/Biplab-Roy/Durga_Darshan/tree/master/app/release",
  },
];

const Portfolio = () => {
  return (
    <section id="portfolio">
      <h5>My Recent Work</h5>
      <h2>Portfolio</h2>
      <div className="container portfolio_container">
        {data.map(({ id, image, title, github, demo }) => {
          return (
            <article key={id} className="portfolio_items">
              <div className="portfolio_item-image">
                <img src={image} alt="portfolio image" />
              </div>
              <h3>{title}</h3>
              <div className="portfolio_items-cta">
                <a href={github} className="btn" target="_blank">
                  Github
                </a>
                <a href={demo} className="btn btn-primary" target="_blank">
                  Live Demo
                </a>
              </div>
            </article>
          );
        })}
      </div>
    </section>
  );
};

export default Portfolio;
